<template>
  <!-- 忘记密码页面 -->
  <div class="register-body">
    <div class="register-box">
      <div class="register-header">
        <h3>忘记密码</h3>
      </div>
      <div class="register-section">
        <a-form
          id="components-form-demo-normal-login"
          :form="form"
          class="login-form"
          @submit="handleSubmit"
          validate-status="validating"
        >
          <a-form-item>
            <a-input
              v-decorator="[
                'phone',
                {
                  rules: [
                    {
                      required: true,
                      pattern: /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
                      message: '手机号不能为空或手机号格式不对',
                    },
                  ],
                },
              ]"
              placeholder="注册时填写的手机号码"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
              v-decorator="[
                'password',
                {
                  rules: [
                    {
                      required: true,
                      pattern: /^(?=.*([a-zA-Z].*))(?=.*[0-9].*)[a-zA-Z0-9-*/+.~!@#$%^&*()]{8,20}/,
                      message:
                        '密码不能为空密码格式首位必须是字母8到20位字母与数字',
                    },
                  ],
                },
              ]"
              type="password"
              placeholder="请输入新密码,首位必须是字母8到20位字母与数字"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
              v-decorator="[
                'passwor',
                {
                  rules: [{ required: true, message: '密码不能为空' }],
                },
              ]"
              type="password"
              placeholder="请再次输入密码"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-row :gutter="8">
              <a-col :span="12">
                <a-input
                  v-decorator="[
                    'captcha',
                    {
                      rules: [
                        {
                          required: true,
                          message: '验证码不能为空',
                        },
                      ],
                    },
                  ]"
                  placeholder="请输入手机验证码"
                />
              </a-col>
              <a-col :span="12">
                <a-button v-show="show" @click="getCode">获取验证码</a-button>
                <a-button v-show="!show">{{ count }} 秒</a-button>
              </a-col>
            </a-row>
          </a-form-item>
          <a-form-item>
            <a-button
              type="primary"
              html-type="submit"
              class="login-form-button"
            >
              确认修改
            </a-button>
          </a-form-item>
        </a-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: "normal_login" });
  },
  data() {
    return {
      show: true,
      count: "",
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields(async (err, values) => {
        if (values.password != values.passwor) {
          return this.$message("两次密码不一样");
        }
        let str = {
          code: values.captcha,
          newPwd: this.$md5(values.password),
          phone: values.phone,
        };

        let { data } = await this.$http.forgo(str);
               if(data.code ==  "0000"){
                   this.$message.success('修改密码成功')
                   this.$router.replace('/')
               }
      });
    },
    getCode() {
      this.form.validateFields((err, values) => {
        let str = {
          mobile: values.phone,
          receiverType: "mobile",
          templateId: 2,
          scene: "",
        };
        this.$http.send(str).then((res) => {
          console.log(res.data);
          if (res.data.code == "0000") {
            const TIME_COUNT = 60;
            if (!this.timer) {
              this.count = TIME_COUNT;
              this.show = false;
              this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= TIME_COUNT) {
                  this.count--;
                } else {
                  this.show = true;
                  clearInterval(this.timer);
                  this.timer = null;
                }
              }, 1000);
            }
          }
        });
      });
    },
    onclicklogin() {
      this.$router.push({
        path: "/",
      });
    },
  },
};
</script>

<style scoped>
#components-form-demo-normal-login .login-form {
  max-width: 300px;
}
#components-form-demo-normal-login .login-form-forgot {
  float: right;
}
#components-form-demo-normal-login .login-form-button {
  width: 100%;
}
.register-body {
  background-image: url("/bg.jpg");
  height: 100vh;
  display: flex;
  align-items: center;
}
.register-box {
  width: 410px;
  margin: 0 auto;
  background: #fff;
  padding: 10px 10px;
}
.register-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.register-section {
  width: 100%;
}
</style>